import React, { useState, useEffect } from "react";
import {
  Button,
  Card,
  CircularProgress,
  Typography,
  Dialog,
  DialogContent,
  TextField,
} from "@mui/material";
import { toast } from "react-toastify";
import { aftBankTransfer } from "../../api/login/Login";
import AftPageLayout from "./AftPageLayout"

const AftBankTransfer = () => {
  const [mobile, setMobile] = useState("");
  const [loading, setLoading] = useState(false);

  
  useEffect(() => {
    const savedMobile = localStorage.getItem("mobile")?.slice(-10);
    if (savedMobile) setMobile(savedMobile);
  }, []);

  const handleSubmit = async () => {
  if (loading) return;

  if (!/^[6-9]\d{9}$/.test(mobile)) {
    toast.error("Enter valid 10 digit mobile number");
    return;
  }

  setLoading(true);
  localStorage.setItem("mobile", mobile);

  try {
    const res = await aftBankTransfer({ mobile });
    const response = res?.data;

    
    if (response?.statuscode === "GRU") {
      window.location.href = response?.data?.abRedirectUrl;
      return;
    }

    
    if (response?.error === false) {
      toast.success(response?.status || "Success");
    } else {
      toast.error(response?.status || "AFT Failed");
    }

  } catch (error) {
    toast.error(
      error?.response?.data?.message || "AFT request failed"
    );
  } finally {
    setLoading(false);
  }
};


  return (
    <AftPageLayout title="AFT Bank Transfer">
      <Card
        sx={{
          p: 3,
          borderRadius: "14px",
          backgroundColor: "var(--bs-primary1)",
          color: "var(--bs-primary2)",
          textAlign: "center",
          maxWidth: "360px",
          margin: "0 auto",
          mt: 4,
          boxShadow: "0 2px 10px rgba(0,0,0,0.2)",
        }}
      >
        <Typography fontWeight={500} mb={2} fontSize="14px">
          Enter Mobile Number for AFT
        </Typography>

        <TextField
          fullWidth
          label="Mobile Number"
          value={mobile}
          onChange={(e) =>
            setMobile(e.target.value.replace(/\D/g, ""))
          }
          inputProps={{ maxLength: 10 }}
          sx={{ mb: 2 }}
        />
        <Typography
  variant="caption"
  sx={{
    display: "block",
    textAlign: "left",
    mb: 2,
    color: "var(--bs-secondary2)",
    lineHeight: 1.4,
  }}
>
  ⚠️ <b>Note:</b> Please enter an Airtel mobile number that is registered with Airtel Payments Bank.  
  <br />
  <b>नोट:</b> कृपया वही एयरटेल मोबाइल नंबर दर्ज करें जो एयरटेल पेमेंट्स बैंक से पंजीकृत हो।
</Typography>

        <Button
          onClick={handleSubmit}
          type="button"
          variant="contained"
          disabled={loading}
          sx={{
            width: "100%",
            padding: "10px",
            borderRadius: "50px",
            backgroundColor: "var(--bs-primary2)",
            color: "var(--bs-primary1)",
            fontWeight: "bold",
          }}
        >
          {loading ? (
            <CircularProgress size={20} color="inherit" />
          ) : (
            "Submit AFT"
          )}
        </Button>
      </Card>
    </AftPageLayout>
  );
};

export default AftBankTransfer;
